
.Header {
    display: flex;
    justify-content: center;
    font-weight: 700;
    background-color: #F6EBDA;
    padding: 10px 0 5px 0;
    @media screen and (min-width: 768px) {
        padding: 10px 0; } }

.container {
    width: 100%;
    max-width: 1140px;
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 0 5px; }
.Logo {
    font-size: 18px;
    @media screen and (min-width: 480px) {
        font-size: 22px; }
    @media screen and (min-width: 768px) {
        font-size: 28px; }
    font-family: 'Dancing Script', cursive; }

.Nav {
    display: flex;
    font-weight: 400;
    padding-top: 3px; }

.Link {
    color: rgb(0, 0, 0);
    font-size: 14px;
    @media screen and (min-width: 480px) {
        font-size: 16px; }
    @media screen and (min-width: 768px) {
        font-size: 20px; }
    // &:not(:last-child)
    margin-right: 10px; }

.Btn {
    background: transparent;
    cursor: pointer;
    &:hover {
        &>svg {
            fill: #FF902A; } } }
